@import url('https://fonts.googleapis.com/css2?family=Prata&display=swap');

.profession {
  color: rgb(14, 13, 17);
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.5;
  display: inline;
  background-image: linear-gradient(
    transparent 75%,
    #f5c21a 40%,
    #dfcf46 90%,
    transparent 85%,
    transparent 100%
  );
  background-repeat: no-repeat;
  background-size: 0% 100%;
  animation: animatedBackground 3s cubic-bezier(0.645, 0.045, 0.355, 1) 0.5s
    forwards;
}

@keyframes animatedBackground {
  to {
    background-size: 100% 100%;
  }
}

// * {
//   position: relative;
//   box-sizing: border-box;
// }

.iframely-embed {
  position: fixed;
  top: 5;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  // background: white;
}

#app {
  // opacity: 0 !important;
  // visibility: hidden;
  z-index: 100;

  &:hover {
    opacity: 0.5;
  }
}

#app {
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  padding-bottom: 4vmin;
  // height: 90vh;
  width: 100%;
  // background: #ede8e2;
  // color: #3a3535;
}

body {
  display: grid;
  // padding: 3vmin;
  // background: #e6ded7;
}

.title {
  // padding-left: 1em;
  grid-column: 1 / -1;
  grid-row: 1;
  // text-align: center;
  font-family: 'Prata', serif;
  font-size: 4vw;
  width: 100%;
  z-index: 2;

  // start

  > .title-inner {
    display: inline-block;
  }
}

@keyframes text-clip {
  from {
    clip-path: polygon(0% 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  to {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
}

@keyframes outer-left {
  from {
    transform: translateX(50%);
  }
  to {
    transform: none;
  }
}

@keyframes inner-left {
  from {
    transform: translateX(-50%);
  }
  to {
    transform: none;
  }
}

.cafe,
.mozart {
  animation: outer-left 1s 1s cubic-bezier(0.5, 0, 0.1, 1) both;
  // outline: 1px solid red;
}

// [class*='inner'] {
//   outline: 1px solid blue;
// }

.title-inner {
  display: inline-block;
  animation: inner-left 1s 1s ease both;
}

.cafe-inner {
  display: inline-block;
  animation: inner-left 1s 1s ease both,
    text-clip 1s 0s cubic-bezier(0.5, 0, 0.1, 1) both;
}

.mozart-inner {
  animation: text-clip 1s 0s cubic-bezier(0.5, 0, 0.1, 1) both;
}

.title {
  animation: outer-left 1s 1s ease both;
}

.cafe {
  // start
  > .cafe-inner {
    display: inline-block;
  }
}

.mozart {
  display: inline-block;
}

.image {
  grid-row: 1;
  grid-column: 2;
  margin-left: -2rem;
  opacity: 0.7;

  animation: image-in 1s cubic-bezier(0.5, 0, 0.1, 1) 2s backwards;

  @keyframes image-in {
    from {
      clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
    }
    to {
      clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

// Replay animation!
body:active * {
  animation: none !important;
}

.resume-link {
  text-decoration: none;
  color: inherit;
  background-image: linear-gradient(to right, #f1e63f, #a18507);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
